$color-green: #3fc060;
$font: sans-serif;
$color-grey: #dddd;
$color-dark-grey: rgba(84, 84, 84);

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: sans-serif;
}

.container {
  width: calc(100% - 120px);
  margin: auto;
}

header {
  .logo {
    h1 {
      font-weight: 400;
      span {
        font-weight: 600;
        color: $color-green;
      }
    }
  }
  .container {
    display: flex;
    align-items: center;
  }
  .searchBar {
    margin-left: auto;
  }
  .header-option {
    font-weight: 500;
    padding-left: 3rem;
  }

  .currentDetails,
  .searchBar {
    display: flex;
  }
}
.options {
  padding: 1.2rem;
  .options-btn {
    background: $color-grey;
    padding: 0.8rem 1rem;
    border-radius: 30px;
    border: 0;
    font-family: $font;
    letter-spacing: 0.6px;
    font-size: 1rem;
    cursor: pointer;

    &.selected {
      background: #000;
      color: #fff;
    }
  }
}

.listings {
  padding: 2rem 0;
  .header {
    display: flex;
    align-items: center;

    .header-title {
      h2 {
        font-weight: 400;
        font-size: 1.8rem;
        margin-bottom: 0.5rem;
        margin-top: 0;
      }
      span {
        color: $color-dark-grey;
      }
    }

    .header-view-options {
      margin: auto 0 auto auto;
      display: flex;
      align-items: center;

      .viewAll {
        color: $color-green;
        padding-right: 2rem;
      }

      .viewMore {
        .arrow {
          width: 40px;
          height: 40px;
          background: $color-grey;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          border-radius: 50%;
          opacity: 0.3;
          transition: all 0.2s ease-in-out;
          cursor: pointer;

          &.dark,
          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }

  .listings-grid {
    .listings-grid-element {
      width: 33%;
      .image {
        height: 240px;
        img {
          width: 100%;
          object-fit: cover;
        }
      }
    }
  }
}
